.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.chessboard{
  padding-left: 1.5rem;
}
.break-line{
  border-top: 2px solid;
  margin: 10px;
}
.study-header{
  margin-bottom: -5px;
}

@media (max-width:840px){
  .responsive-filters{
    width: 100%;
    display: grid;
    grid-template-columns: 100%;    
    text-align: left;
  }
  .responsive-field{
    text-align: left;
    margin-left: 1.5em;
  }
  .resp-search{
    width: 100%;
  }
  .resp-list-item{
    margin-left: -2em;
  }
  .card{
    margin: 0 auto;
  }
  .resp-accordion{
    margin-top: -5em;
    margin-left: 5em;
  }
}